<template>
  <v-dialog
    v-model="showDialog"
    persistent
    max-width="480"
  >
    <v-card
      color="grey lighten-4"
      flat
    >
      <v-card-text>
        <v-container fluid>
          <v-form ref="form">
            <v-text-field
              name=""
              label=""
              v-model="l_tagName"
              required
              :rules="tagNameRule"
            />
          </v-form>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          color="green darken-1"
          flat
          @click.native="saveTag"
        ></v-btn>
        <v-btn
          color="green darken-1"
          flat
          @click.native="cancelTag"
        ></v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<style>
</style>

<script>
export default {
  props: {
    //
    showDialog: {
      type: Boolean,
      default: true
    },
    //tag
    tagName: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      l_showDialog: this.showDialog,
      l_tagName: this.tagName,
      tagNameRule: [v => v !== "" || ""]
    };
  },
  watch: {
    //watch
    tagName(val) {
      this.l_tagName = val;
    }
  },
  computed: {},
  methods: {
    saveTag() {
      if (!this.$refs.form.validate()) {
        // 
      } else {
        this.$emit("save", this.l_tagName);
        this._clear();
      }
    },
    cancelTag() {
      this.$emit("cancel", this.l_tagName);
      this._clear();
    },
    _clear() {
      this.l_tagName = "";
    }
  }
};
</script>


